import React, { Component } from 'react';
import './util.css';


class Sobre extends Component {
  oauth = {};
  constructor (props) {
    super(props)
    let query = new URLSearchParams(document.location.search.substring(1));
    this.state = {
      login: '',
      name: '',
      code: query.get('code')
    }
    this.save = this.save.bind(this)
    this.handleInputChange = this.handleInputChange.bind(this);
  }

  handleInputChange(event) {
    const target = event.target;
    const name = target.name;
    const value = target.value;
    this.setState({
      [name]:value
    })
  }


  save () {
    console.log(JSON.stringify(this.state));
    fetch("https://us-central1-interview-question-27ae9.cloudfunctions.net/addUser",{
      method: 'POST',
      mode: 'cors',
      body: JSON.stringify(this.state),
      headers: { 'Access-Control-Allow-Origin':'*', 
                 'Content-Type': 'application/json',
                 'X-Requested-With':'XMLHttpRequest' }
      })
      .then(function(response) {
        return response.json();
      })
      .then(function(dados) {
        if(dados.status == "OK"){
          console.log("Salvo dados de Login");
          alert("Foi salvo token")
        }
      });
  }
  

  render() {
      return (
      <div className="App">
      <p className=" App-intro">
        Para encerrar informe os dados abaixo.
       </p>
       <input type="text" id="login" name="login" value={this.state.login} onChange={this.handleInputChange} placeholder="Informe seu E-mail"></input><br/><br/>
       <input type="text" id="name" name="name" value={this.state.name} onChange={this.handleInputChange} placeholder="Informe seu nome"></input><br/><br/>
       <button className='button' onClick={this.save}>Salvar Dados</button>
    </div >
 );
  }
}

export default Sobre;