import React, { Component} from 'react';
import './util.css';

class App extends Component {
  constructor () {
    super()
    this.oauth = {
      url: '',
      code: ''
    }
    this.openUrl = this.openUrl.bind(this)
  }

  openUrl () {
    fetch("https://us-central1-interview-question-27ae9.cloudfunctions.net/getLinkHubSpot",{
      method: 'GET',
      mode: 'cors',
      headers: { 'Access-Control-Allow-Origin':'*', 
                 'Content-Type': 'application/json',
                 'X-Requested-With':'XMLHttpRequest' }
      })
      .then(function(response) {
        return response.json();
      })
      .then(function(dados) {
        console.log("Fazendo Login no HubSpot") 
        console.log(dados.url) 
        document.location = dados.url;
      });
  }

  render () {
    return (
      <div>
      <div className='button__container'>
        <button className='button' onClick={this.openUrl}>Fazer Login HUBSPOT</button>
      </div>
      </div>
    )
  }
}
export default App;